import {
    TOP_LOAD_STATE,
    TOP_SET_STATE_FROM_STORAGE,
    TOP_LOAD_NEXT_PAGE
} from '../../actions/actionTypes'

const initialState = {
    results: [],
    page: 1,
    loading: true
}

export default function topReducer(state = initialState, action) {
    switch (action.type) {
        case TOP_SET_STATE_FROM_STORAGE:
            return {
                results: action.results,
                page: action.page,
                loading: false
            }
        case TOP_LOAD_STATE:
            return {
                results: action.results,
                page: action.page,
                loading: action.loading
            }
        case TOP_LOAD_NEXT_PAGE:
            return {
                results: action.results,
                page: action.page,
                loading: action.loading
            }
        default:
            return state
    }
}