import {
    setLocalStorage,
    getLocalStorage
} from '../../components/localStorage/localStorage'

import { 
    POPULAR_SET_STATE_FROM_STORAGE, 
    POPULAR_LOAD_STATE, 
    POPULAR_LOAD_NEXT_PAGE 
} from './actionTypes'

import { POPULAR_URL, POPULAR } from '../../components/config'

export const actionCreatorPopular = () => {
    return async (dispatch) => {
        if (getLocalStorage(POPULAR)) {
            const localState = getLocalStorage(POPULAR)
            return dispatch({
                type: POPULAR_SET_STATE_FROM_STORAGE,
                results: localState.results,
                page: localState.page
            })
        } else {
            const fetched = await fetch(POPULAR_URL(1));
            const data = await fetched.json()

            const newState = {
                results: data.results,
                loading: false,
                page: 1
            }
            setLocalStorage(POPULAR, newState)

            return dispatch({
                type: POPULAR_LOAD_STATE,
                ...newState
            })
        }
    }
}

export const actionCreatorNextPage = () => {
    return async (dispatch, getState) => {
        const {page, results} = getState().popular
        const fetched = await fetch(POPULAR_URL(page+1));
        const data = await fetched.json()

        const newState = {
            results: [...results, ...data.results],
            loading: false,
            page: page+1
        }
        setLocalStorage(POPULAR, newState)
        return dispatch({
            type: POPULAR_LOAD_NEXT_PAGE,
            ...newState
        })
    }
}