import React, { Component } from 'react'
import './TopRated.css'
import Content from '..//Content/Content'
import Loader from '../Loader/Loader'
import ArrowTop from '../ArrowTop/ArrowTop'
import {connect} from 'react-redux'
import {actionCreatorTop, actionCreatorNextPage} from '../../store/actions/actionCreatorsTop'


class TopRated extends Component {
    async componentDidMount() {
        this.props.setData()
    }

    render() {
        return (
            <>
                <h1 className="display-1 mx-auto">Top Rated</h1>
                {
                    this.props.loading
                    ? <Loader/>
                    : <>
                    <div className="container-fluid">
                        <div className="card-deck row justify-content-center my-5">
                            <Content 
                                results={this.props.results}
                                path="/"
                            />
                        </div>
                    </div>
                    <div>
                        <ArrowTop/>
                        <button 
                        className="btn btn-info mx-auto center"
                        onClick={this.props.loadNextPage}
                        >Load more..</button> 
                    </div>
                    </>
                }
            </>
        )
    }
}

function mapStateToProps({top}) {
    return {
        results: top.results,
        page: top.page,
        loading: top.loading
    }
}

function mapDispatchToProps(dispatch) {
    return {
        setData: () => dispatch(actionCreatorTop()),
        loadNextPage: () => dispatch(actionCreatorNextPage())
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(TopRated);