import {
    setLocalStorage,
    getLocalStorage
} from '../../components/localStorage/localStorage'

import { TOP_SET_STATE_FROM_STORAGE, TOP_LOAD_STATE, TOP_LOAD_NEXT_PAGE } from './actionTypes'

import { TOP_URL, TOP } from '../../components/config'

export const actionCreatorTop = () => {
    return async (dispatch) => {
        if (getLocalStorage(TOP)) {
            const localState = getLocalStorage(TOP)
            return dispatch({
                type: TOP_SET_STATE_FROM_STORAGE,
                results: localState.results,
                page: localState.page
            })
        } else {
            const fetched = await fetch(TOP_URL(1));
            const data = await fetched.json()

            const newState = {
                results: data.results,
                loading: false,
                page: 1
            }
            setLocalStorage(TOP, newState)

            return dispatch({
                type: TOP_LOAD_STATE,
                ...newState
            })
        }
    }
}

export const actionCreatorNextPage = () => {
    return async (dispatch, getState) => {
        const {page, results} = getState().top
        const fetched = await fetch(TOP_URL(page+1));
        const data = await fetched.json()

        const newState = {
            results: [...results, ...data.results],
            loading: false,
            page: page+1
        }
        setLocalStorage(TOP, newState)
        return dispatch({
            type: TOP_LOAD_NEXT_PAGE,
            ...newState
        })
    }
}