import {
  SET_RESULTS_TRAILER, 
  SET_MOVIE_BADGES,
  RELOAD
} from '../../actions/actionTypes'


const initialState = {
  results: [],
  trailer: "",
  badges: [
    "success",
    "warning",
    "danger",
    "primary",
    "info",
    "secondary",
    "dark"
  ],
  movieBadges: []
}

export default function movieReducer(state = initialState, action) {
  switch (action.type) {
    case SET_RESULTS_TRAILER:
      return {
        ...state, 
        results: action.payloadResults,
        trailer: action.payloadTrailer
      }
    case SET_MOVIE_BADGES:
      return {
        ...state, movieBadges: action.payload
      }
    case RELOAD:
      return {
        ...state, 
        results: [],
        trailer: "",
        movieBadges: []
      }
    default:
      return state
  }
}