import React from 'react'
import './Movie.css'
import image from './image.jpg'
import { IMAGE_URL, EXTEND_SIZE } from '..//config'
import Loader from '../Loader/Loader'
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd'
import { connect } from 'react-redux'
import actionCreatorMovie from '../../store/actions/actionCreatorsMovie'

import { SET_MOVIE_BADGES, RELOAD } from '../../store/actions/actionTypes'

class Movie extends React.Component {

    componentWillUnmount() {
        this.props.reload()
    }

    componentDidMount() {
        this.props.setData(this.props.match.params.movie, this.setBadges.bind(this))
    }

    setBadges(results) {
        let badgesId = [];
        for (let i = 0; i < results.genres.length; i++) {
            let currBadge = Math.round(Math.random() * 6)
            badgesId.some( b => currBadge === b) ? i-- : badgesId.push(currBadge)
        }

        const badges = results.genres.map((genre, id) => (
            <Draggable draggableId={id + genre.name} index={id} key={id}>
                {(provided) => (
                    <span
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        ref={provided.innerRef}
                        key={id}
                        className={`badge badge-pill badge-${this.props.badges[badgesId[id]]}`}
                    >
                        {genre.name}
                    </span>
                )}
            </Draggable>
        ))
        this.props.setChangeMovieBadges(badges)
    }

    averageRate() {
        return Math.round(this.props.results.vote_average);
    }

    setRate() {
        const movieRate = this.averageRate() || 0;
        const stars = [];
        for (let i = 0; i < 10; i++) {
            stars[i] = (
                <span
                    key={i}
                    className={`fa fa-star${i < movieRate 
                    ? " checked" 
                    : ""}`}>
                </span>
            )
        }
        return stars
    }

    onDragEnd = ({ destination, source }) => {
        if (!destination) return;
        if (destination.index === source.index) return;
        const newStateBadges = [...this.props.movieBadges]
        const replacedItemFrom = newStateBadges[source.index]
        newStateBadges.splice(source.index, 1)
        newStateBadges.splice(destination.index, 0, replacedItemFrom)
        this.props.setChangeMovieBadges(newStateBadges)
    }


    render = () => {
        return (
            <div className="container row new-row">
                {
                    this.props.trailer
                        ?
                        <img src={this.props.results.poster_path
                            ? `${IMAGE_URL}${EXTEND_SIZE}${this.props.results.poster_path}`
                            : image} className="rounded col-xl-4 offset-md-1" alt="movie-post" />
                        : <Loader />
                }
                <div className="col-xl-4 offset-xl-1">
                    <h1>{this.props.results.title}</h1>
                    <hr />
                    <strong> Description: </strong>
                    <p>
                        {this.props.results.overview || "loading.."}
                    </p>
                    <hr />
                    <DragDropContext onDragEnd={this.onDragEnd}>
                        <div >
                            <strong>Generes: </strong>
                            {   
                                <Droppable droppableId="droppable" direction="horizontal">
                                    {(provided) => (
                                        <span
                                            ref={provided.innerRef}
                                            {...provided.droppableProps}
                                        >
                                            {this.props.movieBadges}
                                            {provided.placeholder}
                                        </span>
                                    )}
                                </Droppable>
                            }
                        </div>
                    </DragDropContext>
                    <hr />
                    <div><strong>Rate: </strong><i>{this.averageRate() || "?"}/10</i></div>
                    {this.setRate()}
                    <hr />
                    <div>
                        <strong> Trailer: </strong>
                    </div>
                    <div className="trailer">
                        {
                            this.props.trailer
                                ? (<iframe frameBorder="0" allowFullScreen="1"
                                    title="YouTube video player"
                                    src={`https://www.youtube.com/embed/${this.props.trailer}?controls=1`} />)
                                : "loading..."
                        }
                    </div>
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        results: state.movie.results,
        trailer: state.movie.trailer,
        badges: state.movie.badges,
        movieBadges: state.movie.movieBadges
    }
}

function mapDispatchToProps(dispatch) {
    return {
        setData: (movieId, callback) => dispatch(actionCreatorMovie(movieId, callback)),
        setChangeMovieBadges: (payload) => dispatch({ type: SET_MOVIE_BADGES, payload }),
        reload: () => dispatch({ type: RELOAD })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Movie)