import {
    setLocalStorage,
    getLocalStorage
} from '../../components/localStorage/localStorage'

import { SET_RESULTS_TRAILER } from './actionTypes'

import {
    API_URL,
    API_KEY,
    YOUTUBE_URL,
    DEFAULT_TRAILER
} from '../../components/config'

const actionCreatorMovie = (movieId, callback) => {
    return async (dispatch) => {
        const movieLocalState = getLocalStorage(movieId)
        if (movieLocalState) {
            callback(movieLocalState.results)
            return dispatch({
                type: SET_RESULTS_TRAILER,
                payloadTrailer: movieLocalState.trailer,
                payloadResults: movieLocalState.results
            })
        } else {
            const urlData = `${API_URL}movie/${movieId}?api_key=${API_KEY}`

            const fetchInformation = await fetch(urlData);
            const results = await fetchInformation.json()

            const fetchTrailer = await fetch(YOUTUBE_URL(movieId));
            const resultsTrailer = await fetchTrailer.json()

            const trailer = resultsTrailer.results[0]
                ? resultsTrailer.results[0].key : DEFAULT_TRAILER;

            setLocalStorage(movieId, { results, trailer })
            callback(results)
            return dispatch({
                type: SET_RESULTS_TRAILER,
                payloadTrailer: trailer,
                payloadResults: results
            })
        }
    }
}

export default actionCreatorMovie
