import React, { Component } from 'react'
import Content from '..//Content/Content'
import './Popular.css'
import Loader from '../Loader/Loader'
import ArrowTop from '../ArrowTop/ArrowTop'
import {connect} from 'react-redux'
import {actionCreatorPopular, actionCreatorNextPage} from '../../store/actions/actionCreatorsPopular'


class Popular extends Component {
    async componentDidMount() {
        this.props.setData()
    }

    render() {
        return (
            <>
                <h1 className="display-1 mx-auto">Popular</h1>
                {
                    this.props.loading
                        ? <Loader />
                        : <><div className="container-fluid">
                            <div className="card-deck row justify-content-center my-5">
                                <Content
                                    results={this.props.results}
                                    path="/"
                                />
                            </div>
                        </div>
                        <div>
                            <ArrowTop/>
                            <button
                                className="btn btn-info mx-auto center"
                                onClick={this.props.loadNextPage}
                                >Load more..</button>
                        </div>
                        </>
                }
            </>
        )
    }
}

function mapStateToProps({popular}) {
    return {
        results: popular.results,
        page: popular.page,
        loading: popular.loading
    }
}

function mapDispatchToProps(dispatch) {
    return {
        setData: () => dispatch(actionCreatorPopular()),
        loadNextPage: () => dispatch(actionCreatorNextPage())
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Popular);