import {
    POPULAR_LOAD_STATE,
    POPULAR_SET_STATE_FROM_STORAGE,
    POPULAR_LOAD_NEXT_PAGE
} from '../../actions/actionTypes'

const initialState = {
    results: [],
    page: 1,
    loading: true
}

export default function popularReducer(state = initialState, action) {
    switch (action.type) {
        case POPULAR_SET_STATE_FROM_STORAGE:
            return {
                results: action.results,
                page: action.page,
                loading: false
            }
        case POPULAR_LOAD_STATE:
            return {
                results: action.results,
                page: action.page,
                loading: action.loading
            }
        case POPULAR_LOAD_NEXT_PAGE:
            return {
                results: action.results,
                page: action.page,
                loading: action.loading
            }
        default:
            return state
    }
}